
@keyframes playing{
0%{transform:scale(0.5)
}
25%{transform:scale(1)
}
50%{transform:scale(0.5)
}
75%{transform:scale(1)
}
100%{transform:scale(0.5)
}
}
.audio-player{cursor:pointer;text-align:left;font-size:14px;padding:10px;height:20px;line-height:20px
}
.audio-player .icon-wave-right{vertical-align:middle;margin-right:0.7rem;display:inline-block
}
.audio-player .icon-wave-right::before{color:inherit
}
.audio-player audio{display:none
}
.audio-player.playing .icon-wave-right{animation-name:playing;animation-duration:3s;animation-iteration-count:infinite
}



